	&__text {
		@extend %fontOpenSans;
		font-size: 22px;
		color: rgba(#292929, 0.7);
	}
}
// copyright :: end

// social
.social {
	display: flex;
	align-items: center;

	&--footer {}

	&__title {
		margin-right: 91px;
		font-size: 36px;
		color: #000;
	}

	&__list {
		display: flex;
		align-items: center;

		&-item {
			margin-right: 60px;
		}

		a {
			display: flex;

			.icon-font {
				color: #000000;
				@include transition(color);

				&:hover {
					@include transition(color);
				}

				&.icon {
					&-facebook {
						font-size: 33px;

						&:hover {
							color: #3b5999;
						}
					}
					&-instagram {
						font-size: 36px;

						&:hover {
							color: #e4405f;
						}
					}
					&-twitter {
						font-size: 40px;

						&:hover {
							color: #55acee;
						}
					}
					&-linkedin {
						font-size: 37px;

						&:hover {
							color: #0077B5;
						}
					}
					&-youtube {
						font-size: 46px;

						&:hover {
							color: #cd201f;
						}
					}
				}
			}
		}
	}
}
// social :: end

